.footerBottom {
    display: flex;
    width: 1070px;
    align-items: center;
    justify-content: space-between;
    border-top: 1.5px solid #4C565E;
    padding: 27px;
}

.footerItem {
    font-family: 'Gilroy';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 29px;
    color: #878F99
}

.footerMainList {
    padding-top: 52px;
}

.footerList {
    list-style-type: none;
}

.footerListHeader {
    margin-left: 38px;
    font-family: 'Gilroy';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;
    color: #FFFFFF;
}

.footerNoContainer {
    width: 100%;
    background-color: #33383C;
}

.footerBottomText {
    font-family: 'Gilroy';
    font-size: 15px;
    line-height: 18px;
    color: #66717A;
}

.footerBottomText:hover {
    color: #05F3B3;
}

.footerBottomLink {
    text-decoration: none;
}

.footerTopNoContainer {
    background-color: #282B30;
}

.footertopText1 {
    font-family: 'Gilroy';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;
    color: #FFFFFF;
    margin-right: 149px;
}

.footertopText2 {
    font-family: 'Gilroy';
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 18px;
    color: #98A4B7;
}

.footertopimg {
    margin-right: 14px;
    margin-top: 25px;
    margin-bottom: 25px;
}